export default {
	components: {
	},
	data() {
		return {
			active: 1
		};
	},
	onPageScroll(Object) {},
	methods: {
		changeActive: function(e) {
			if (e == 1) {
				this.active = 1;
				uni.navigateTo({
					url:'../payment/payment'
				});
			} else {
				this.active = 2;
			}
		},
		goRoom: function() {
			uni.navigateTo({
				url: '../roomDetails/roomDetails'
			});
		}
	}
};
